import Vue from 'vue'
import Router from 'vue-router'

Vue.use(Router)

export default new Router({
  routes: [
    {
      path: '/',
      name: 'Login',
      component: () => import('@/pages/login/Login')
    },
    {
      path: '/usermain',
      name: 'UserMain',
      meta: {
        requireAuth: true
      },
      component: () => import('@/pages/user/Main')
    },
    {
      path: '/adminmain',
      name: 'AdminMain',
      meta: {
        requireAuth: true
      },
      component: () => import ('@/pages/admin/Main')
    },
    {
      path: '/settingmain',
      name: 'SettingMain',
      meta: {
        requireAuth: true
      },
      component: () => import ('@/pages/setting/Main')
    }

  ]
})




